<template>
  <section class="section-1 section-padding">
    <div class="fixed fixed_center">
      <h2 class="s1__title title">
        All recipes
      </h2>
      <div class="s1__btns">
        <div class="btns-list">
          <router-link to="/recipes/new-recipe" class="btn"
            >Add New Recipe</router-link
          >
        </div>
      </div>
      <ul class="list-1">
        <recipe-card
          v-for="recipe in recipes"
          :key="recipe.id"
          :title="recipe.title"
          :img="recipe.img"
          :id="recipe.id"
        ></recipe-card>
      </ul>
    </div>
  </section>
</template>

<script>
import recipeCard from '../components/recipes/RecipeCard.vue';
import { useStore } from 'vuex';
export default {
  components: {
    recipeCard
  },
  setup() {
    const store = useStore();
    const recipes = store.getters['recipes/getRecipes'];
    return {
      recipes
    };
  }
};
</script>
