<template>
  <form class="sf__form review-form" @submit.prevent="submitForm">
    <label class="label-1">
      <div class="label-1__title descr">Write your name</div>
      <input type="text" class="input-1 descr" v-model="data.author" />
    </label>
    <label class="label-1">
      <div class="label-1__title descr">Write your review</div>
      <textarea class="input-1 descr" v-model="data.descr"></textarea>
    </label>
    <button class="btn descr">Submit</button>
  </form>
</template>

<script>
import { reactive } from 'vue';
export default {
  props: ['id'],
  emits: ['load-data'],
  setup(props, context) {
    const data = reactive({
      id: new Date().getTime().toString(),
      author: '',
      descr: '',
      recipeId: props.id
    });
    function submitForm() {
      context.emit('load-data', data);
    }
    return {
      data,
      submitForm
    };
  }
};
</script>
