<template>
  <section class="new-recipe section-padding">
    <div class="fixed fixed_center">
      <h2 class="nr__title title">Add New Recipe</h2>
      <form @submit.prevent="validateForm" class="recipe-form">
        <label class="label-1 ">
          <div class="label-1__title descr">Enter the name of recipe</div>
          <input type="text" class="input-1 descr" v-model="title" />
        </label>
        <label class="label-1 ">
          <div class="label-1__title descr">Enter step by step description</div>
          <textarea class="input-1 descr" v-model="descr"></textarea>
        </label>
        <input type="file" accept="image/*" />
        <button class="btn descr">Submit</button>
      </form>
    </div>
  </section>
</template>
<script>
import { ref } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
export default {
  setup() {
    const router = useRouter();
    const store = useStore();
    const title = ref(''),
      descr = ref('');
    function validateForm() {
      const data = {
        title: title.value,
        descr: descr.value
      };

      store.dispatch('recipes/setRecipe', data);
      router.replace('/');
    }
    return {
      validateForm,
      title,
      descr
    };
  }
};
</script>
