<template>
  <li class="recipe-card">
    <div class="rc__img">
      <img :src="imgLink" alt="" />
    </div>
    <h2 class="rc__title title">{{ title }}</h2>
    <router-link :to="recipeLink" class="rc__btn btn btn_white-border"
      >See More</router-link
    >
  </li>
</template>

<script>
import { computed } from 'vue';
export default {
  props: ['title', 'img', 'id'],
  setup(props) {
    const imgLink = computed(() => {
      return `/img/${props.img}`;
    });
    const recipeLink = `/recipes/${props.id}`;

    return {
      imgLink,
      recipeLink
    };
  }
};
</script>
