<template>
  <section class="recipe-page section-padding">
    <div class="fixed">
      <div class="rp__img">
        <img :src="imgLink" alt="" />
      </div>
      <h2 class="rp__title title">
        {{ selectedrecipe.title }}
      </h2>
      <div class="rp__descr descr">
        <p>
          <strong> Step by step description: <br /> </strong>
        </p>
        <p>{{ selectedrecipe.descr }}</p>
      </div>
    </div>
  </section>
  <section class="section-review section-padding">
    <div class="fixed">
      <h2 class="sf__title title">Write a review</h2>
      <review-form @load-data="addReview" :id="id"></review-form>

      <ul v-if="isReviews" class="sf__list list-1">
        <review-card
          v-for="review in reviews"
          :key="review.id"
          :author="review.author"
          :descr="review.descr"
        ></review-card>
      </ul>
      <div v-else class="descr">Be the first one who leave a review!</div>
      <base-dialog :show="showDialog">Thank you for review!</base-dialog>
    </div>
  </section>
</template>

<script>
import ReviewCard from '../components/reviews/ReviewCard.vue';
import ReviewForm from '../components/reviews/ReviewForm.vue';
import { useStore } from 'vuex';
import { ref, computed } from 'vue';
export default {
  components: {
    ReviewCard,
    ReviewForm
  },
  props: ['id'],
  setup(props) {
    const store = useStore();
    const selectedrecipe = store.getters['recipes/getRecipes'].find(
      recept => recept.id === props.id
    );
    const imgLink = `/img/${selectedrecipe.img}`;

    let showDialog = ref(false);

    let reviews = ref(
      store.getters['reviews/getReviews'].filter(
        review => review.recipeId === props.id
      )
    );

    const isReviews = computed(() => {
      if (reviews.value.length > 0) {
        return true;
      }
      return false;
    });

    function addReview(data) {
      store.dispatch('reviews/addReview', data);
      reviews.value.unshift(data);
      console.log(reviews.value);
      showDialog.value = true;
      setTimeout(() => {
        showDialog.value = false;
      }, 1000);
    }

    return {
      selectedrecipe,
      imgLink,
      reviews,
      addReview,
      showDialog,
      isReviews
    };
  }
};
</script>
